@font-face {
  font-family: "maple_blod";
  src: url("../font/Maplestory\ Bold.ttf") format("truetype");
}
.container input {
  font-family: "maple_blod";
  margin-top: 100px;
  margin-bottom: 30px;
  width: 100%;
  border: 2px solid rgba(black, 0.3);
  font-size: 4em;
  padding: 0 1em;
  border-radius: 1em;
  box-sizing: border-box;
  box-shadow: rgba(black, 0.3) 10px 10px 15px;
}
